@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@function width($element, $full) {
  @return ($element*100/$full); }

@function get-vh($target) {
    $vh-context: ($max-height-breakpoint * .01) * 1px;
    @return ($target/$vh-context) * 1vh; }

@function get-vw($target) {
    $vw-context: ($max-width-breakpoint * .01) * 1px;
    @return ($target/$vw-context) * 1vw; }

@mixin vw($size) {
    font-size: get-vw($size);
    @media #{$wide-screen} {
        font-size: $size; } }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin md {
  @include r(1023) {
    @content; } }

@mixin sm {
  @include r(767) {
    @content; } }

@mixin xs {
  @include r(599) {
    @content; } }
