.header {
    width: 100%;
    height: 100vh;
    min-height: 800px;
    padding: em(14);
    @include cover;
    .header__slider {
        width: 100%;
        height: 100%;
        border: 1px solid $gold;
        display: flex;
        flex-direction: column;
        position: relative;
        .logo {
            position: absolute;
            bottom: 400px;
            left: 50%;
            transform: translate(-50%);
            .logo__img {
                width: 250px;
                @include md {
                    width: 230px; }
                @include sm {
                    width: 210px; }
                @include xs {
                    width: 180px; } } }
        .titles {
            width: 100%;
            position: absolute;
            bottom: 190px;
            .title {
                font-size: em(48);
                color: #fff;
                text-align: center;
                @include md {
                    font-size: em(46); }
                @include sm {
                    font-size: em(42); }
                @include xs {
                    font-size: em(38); } } }
        .dots {
            text-align: center;
            bottom: 125px; }
        .scroll {
            width: 100%;
            color: #fff;
            position: absolute;
            bottom: 40px;
            .scroll__content {
                text-align: center;
                .scroll__text {
                    display: inline-block;
                    overflow: hidden;
                    font-size: em(14); }
                .scroll__icon {
                    display: inline-block;
                    padding: 0 10px;
                    .icon-mouse {
                        @include s(mouse); } } } } } }


