.comments {
    width: 100%;
    height: 370px;
    border: 23px solid $gold;
    .comments__wrapper {
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        .comments__content {
            width: 100%;
            max-width: 815px;
            margin: auto;
            .title {
                position: relative;
                .text {
                    font-family: $charter-regular;
                    text-align: center;
                    position: relative;
                    @include sm {
                        font-size: em(14); } }
                .meta {
                    margin: 0 auto;
                    display: table;
                    .author {
                        display: inline-block;
                        overflow: hidden;
                        text-align: right;
                        display: table-cell;
                        vertical-align: middle;
                        .author__name {
                            font-size: em(20);
                            font-weight: 500;
                            color: $gold;
                            @include sm {
                                font-size: em(16); } }
                        .author__position {
                            @include sm {
                                font-size: em(14); } } }
                    .signature {
                        width: 170px;
                        height: 70px;
                        @include cover;
                        display: inline-block;
                        overflow: hidden; } } } } } }


