.history {
    width: 100%;
    min-height: 665px;
    position: relative;
    overflow: hidden;
    @include image('history_bg.jpg');
    @include cover;
    .history__wrapper {
        width: 100%;
        margin: 0 auto;
        @include vcenter;
        .history__content {
            width: percentage(1066/1680);
            min-width: 244px;
            background-color: #fff;
            padding: 20px;
            text-align: center;
            @include vcenter;
            .banner {
                width: 100%;
                height: 100%;
                border: 1px solid $gold;
                @include md {
                    border: none; }
                .banner__text {
                    max-width: 517px;
                    height: 100%;
                    margin: 0 auto;
                    padding: 90px 0;
                    position: relative;
                    .banner__year {
                        width: 100%;
                        border-top: 2px solid $gold;
                        border-bottom: 2px solid $gold;
                        color: $gold;
                        position: relative;
                        .banner__text-small {
                            width: 100%;
                            font-size: em(20);
                            line-height: 20px;
                            position: absolute;
                            span {
                                background-color: #fff;
                                padding: 0 10px;
                                display: inline-block; } }
                        .banner__text-small-top {
                            top: -20px; }
                        .banner__text-small-bottom {
                            bottom: -10px; }
                        .year {
                            font-family: $charter-bold;
                            font-size: em(136);
                            font-weight: 600;
                            @include md {
                                font-size: em(126); }
                            @include sm {
                                font-size: em(116); }
                            @include xs {
                                font-size: em(106); }
                            @include r(489) {
                                font-size: em(86); } } }
                    .banner__text-small {
                        font-size: em(20);
                        color: $gold;
                        position: relative;
                        bottom: -15px; }
                    .banner__info {
                        font-family: $charter-regular;
                        position: relative;
                        bottom: -40px; } } } } } }
