.tech {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    .tech__wrapper {
        width: 50%;
        padding: 20px;
        @include md {
            width: 100%; } }
    .tech__wrapper-info {
        padding-right: 0;
        @include md {
            padding-right: 20px; }
        .tech__info {
            height: 100%;
            border: 1px solid $gold;
            border-right: none;
            display: flex;
            @include md {
                border-right: 1px solid $gold; }
            .tech__info-content {
                width: 100%;
                max-width: 517px;
                margin: auto;
                .tech__title {
                    position: relative;
                    .title {
                        padding: 20px 35px;
                        margin-left: 5px;
                        border-bottom: 2px solid $gold;
                        font-family: $ptsans-narrow-bold;
                        font-size: em(30);
                        text-transform: uppercase; }
                    .line {
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        display: block;
                        width: percentage(380/517);
                        height: 4px;
                        background-color: #fff; } }

                .tech__text {
                    padding: 20px 35px;
                    font-family: $charter-regular;
                    text-align: left;
                    .highlighted {
                        font-family: $charter-bold;
                        color: $gold; } } } } }

    .tech__wrapper-slider {
        height: 665px;
        padding-left: 0;
        @include cover;
        @include md {
            padding-left: 20px; }
        .tech__slider {
            height: 100%;
            border: 1px solid $gold;
            border-left: none;
            position: relative;
            @include md {
                border-left: 1px solid $gold; } } } }
